















































import uniSegmentedControl from '@/components/uni-segmented-control.vue';
var dateUtils = require('@/common/util.js').dateUtils;
import uniLoadMore from '@/components/uni-load-more.vue';
var userLoginInfo;
export default {
	components: {
		uniSegmentedControl,
		uniLoadMore
	},
	data() {
		return {
			type: 1,
			goodsList: [],
			loadingType: 1,
			showLoadMore: false,
			page: 1,
			items: [{
					name: '宝贝收藏',
					type: 1
				},
				{
					name: '内容收藏',
					type: 0
				}
			],
			current: 0
		}
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../collection/collection', 1);
		this.cateCurrentIndex = this.status;
		this.getLikeGoodsList();
	},
	onLoad() {
		// this.getLikeGoodsList();
		this.goodsList = [],
		this.showLoadMore = false;
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		//console.log("onReachBottom");
		this.getLikeGoodsList();
	},
	methods: {
		onClickItem(e) {
			if (this.current !== e.index) {
				this.current = e.index;
				this.type = e.type;
				// 清空商品信息			
				this.goodsList = [];
				this.page = 1;
				this.getLikeGoodsList();
			}
		},
		getLikeGoodsList: function() {
			uni.request({
				url: this.apiUrl + 'user/getCollect',
				method: "GET",
				data: {
					uid: userLoginInfo.id,
					type: this.type,
					page: this.page
				},
				success: (res) => {
					if (res.data.code === 0) {
						if (Array.isArray(res.data.data) && res.data.data.length === 0) {
							this.loadingType = 2;
							return;
						}
						if (res.data.data.length < 10) {
							this.loadingType = 2;
						}
						this.showLoadMore = true;
						this.page++;
						let collectionData = [];
						res.data.data.forEach((orders) => {
							collectionData.push(orders);
						});
						console.log(res.data.data);
						// 拼接加载更多数据
						this.goodsList = this.goodsList.concat(collectionData);
					}
				}
			})
		}
	}
}
